  import Distpicker from '@/components/Distpicker.vue';
  const wangEditor = require('wangeditor');
//  import editor from 'vue2-medium-editor'
  export default {
    name: 'BaseInfo',
    data() {
      return {
        msg: 'this is ',
        step: 1,
        placeholders: {
          province: '----',
          city: '----',
        },
        picValue: '',
        // teacherInfo: {
        //   realName: '', //  *
        //   hdsex: '', //  *
        //   sfdq_tj: '', //  *
        //   csdq_tj: 'eee', //  *
        //   mobile: '', //  *
        //   pic: '', // 
        //   qq: '', // qq
        //   email: '', // 
        //   blog: '', // 
        //   weibo: '', // 
        //   intro: '', // *
        //   code: '', // 
        //   notice: '', // 
        //   notice_type: '', // 
        //   notice_info: '', // 
        //   level: '', //  zhibo
        //   //orgnamebusDepartment  jobCode 
        //   orgname: '', // 
        //   busDepartment: '', // 
        //   jobCode: '' // 
        // },
        editor: '',
        intro: '',
      }
    },
    computed: {
      teacher() {
        let serverDataP = this.$store.state.teacherInfo;
        

        let obj = {};
        if(serverDataP.Partner) {
          let serverData = serverDataP.Partner;

          obj = {
            realName:  serverData.truename , //  *
            hdsex:  serverData.sex, //  *
            sfdq_tj:  serverData.province, //  *
            csdq_tj: serverData.city, //  *
            mobile:  serverDataP.phone, //  *
            pic:  serverDataP.photo, // 
            email:  serverData.email, // 
            blog:  serverData.blog, // 
            weibo:  serverData.weibo, // 
            intro:  serverData.intro, // *
            code:  serverData.notice, // 
            notice:  serverData.notice, // 
            notice_type:  serverData.noticeType, // 
            notice_info:  serverData.noticeInfo, // 
            level:  serverData.level, //  zhibo
            //orgnamebusDepartment  jobCode 
            orgname:  serverData.orgname, // 
            busDepartment:  serverData.busDepartment, // 
            jobCode:  serverData.jobCode, // 
          }

          // this.placeholders = {
          //   province: locaData.sfdq_tj || serverData.province ,
          //   city: locaData.csdq_tj || serverData.city ,
          // }

          this.intro = obj.intro
        }
        return obj;
      },
      pid() {
        return this.$store.state.partnerId;
      },
      webOld() {
        return this.$store.state.oldHost;
      },
      userLevel() {
        return this.$store.state.userLevel;
      }

    },
    watch: {
      teacher: {
        // console.log(this.teacherInfo.csdq_tj)
        deet: true,
        handler() {
          console.log(this.teacher.csdq_tj)
        }
      },
      intro(val) {
        this.teacher.intro = val;
      }
    },
    components: {
      Distpicker,
    },
    methods: {
      enevtNextStep() {
//      :href="`${webOld}/teacher/admin/TagSelect.html`"
        // if(this.userLevel !== 1) {
        //   this.$store.dispatch('UPGRADE');
        //   return false;
        // }
        let isV = true;
        const data = this.teacher;
        // 
        const testData = {
            realName:{
              // test:/^[\u4e00-\u9fa5]{2,10}$/,
              test:/\S/,
              tips: ''
            }, //  *
            hdsex: {
              test:/\S/,
              tips: ''
            }, //  *
            sfdq_tj: {
              test: /\S/,
              tips: ''
            }, //  *
            csdq_tj: {
              test:/\S/,
              tip: ''
            }, //  *
            // mobile: {
            //   test:/0?(13|14|15|18|17)[0-9]{9}/,
            //   tips: ''
            // }, //  *

//            code: '', // 
//            notice: '', // 
//            notice_type: '', // 
//            notice_info: '', // 
//            level: '', //  zhibo
//            //orgnamebusDepartment  jobCode 
//            orgname: '', // 
//            busDepartment: '', // 
//            jobCode: '' // 
        };
        // 
        const testAid1 = {
          qq: {
            test:/[1-9]([0-9]{5,11})/,
            tips: 'qq'
          }, // qq
          email: {
            test:/\w[-\w.+]*@([A-Za-z0-9][-A-Za-z0-9]+\.)+[A-Za-z]{2,14}/,
            tips: ''
          }, // 
          blog: {
            test:/^((https|http|ftp|rtsp|mms)?:\/\/)[^\s]+/,
            tips:''
          }, // 

          intro: {
            test: /^(?=.*?\S)[\s\S]{50,250}$/g,
            tips: ''
          }, // *
        };
        const testAid = {
          orgname: {
            test:/\S/,
            tips: ''
          }, // 
          busDepartment: {
            test:/\S/,
            tips: ''
          }, // 
          jobCode: {
            test:/^[AS][0-9]{13}/,
            tips:'/AS+13'
          } // 
        };

        for( let i in data) {
          if(testData[i] && !testData[i].test.test(data[i])) {
            this.$store.dispatch('OPEN_SYS_DIALOG', {
              title:'',
              errorMsg: testData[i].tips
            });
            return false;
          }

          if(data[i] && testAid1[i] && !testAid1[i].test.test(data[i])) {
            this.$store.dispatch('OPEN_SYS_DIALOG', {
              title:'',
              errorMsg: testAid1[i].tips
            });
            return false;
          }
        }

        if(testAid.orgname.test.test(data['orgname'])) {
          for(let l in testAid) {
            if(!testAid[l].test.test(data[l])) {
              this.$store.dispatch('OPEN_SYS_DIALOG', {
                title:'',
                errorMsg: testAid[l].tips
              });
              return false
            }
          }

        }
        if(isV) {
          this.$store.dispatch('FILL_DETAIL_INFO', data).then(
            window.open(`${this.webOld}/teacher/admin/TagSelect.html`)
          )
        }
      },
      eventFileUp(event) {

        const pid = this.pid;
        let h5 = new H5_upload(pid);
        h5.init();
        h5.attributes(event.target).then(
          res => {
            this.editor.command(event, 'insertHtml', `<img src="${res}" />`);
            this.teacher.notice_info = this.editor.$txt.html()
          }
        )
      },
      onSelected(value) {
       this.teacher.sfdq_tj = value.province;
       this.teacher.csdq_tj = value.city;
      }
    },
    created() {
      this.$store.dispatch('GET_PARTNER_ID').then(
        () => {
          this.$store.dispatch('GET_TEACHER_INFO')
        }
      )
    },
    mounted() {
//      const pid = this.pid;
//      new H5_upload(pid).init();;
//      ajax({
//        url: _self.oringin+e,
//        data: fd,
//        type:'POST',
//        timeout: _self.timerCancel,
//        crossDomain: true,
//        processData: false,
//        contentType: false,

        this.editor = new wangEditor('editor-trigger');
        this.editor.config.menus = [];

        this.editor.create();
//      editor.command(e, 'insertHtml', s);
    }
  }
